rap text-sm font-medium text-gray-900\">\r\n                        Cash in Hand\r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(totalCashInHand) }}\r\n                      </td> \r\n                    </tr>\r\n\r\n \r\n                  </tbody>\r\n                </table>\r\n              </div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n        <!-- Daily Cash Flow ================ -->\r\n\r\n        <!-- Expense Details ================ -->\r\n        <div class=\"flex flex-col w-full\">\r\n          <h1 class=\"text-lg leading-6 font-medium text-gray-900 mb-2\">Expenses Details</h1>\r\n          <div class=\"-my-2 overflow-x-auto sm:-mx-6 lg:-mx-8 \">\r\n            <div class=\"py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8\">\r\n              <div class=\"shadow overflow-hidden border-b border-gray-200 sm:rounded-lg mb-10\">\r\n                <table class=\"min-w-full divide-y divide-gray-200\">\r\n                  <thead class=\"bg-gray-200\">\r\n                    <tr>\r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                        Title\r\n                      </th>\r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                        Amount \r\n                        <button type=\"button\" @click.prevent=\"addExpense\" class=\"inline-flex items-center px-2.5 py-1.5 border border-transparent text-xs font-medium rounded shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500\">\r\n                           Add\r\n                        </button>\r\n                      </th>\r\n                      \r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                       Action\r\n                      </th>\r\n                    </tr>\r\n                  </thead>\r\n                  <tbody>\r\n                     \r\n                    <tr class=\"bg-white\" v-for=\"(e,i) in form.expense_details\">\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                        <text-input v-model=\"e.title\" type=\"text\" class-input=\"py-1\" label=\"\" />\r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        <text-input v-model=\"e.amount\" type=\"text\" class-input=\"py-1\" label=\"\" />\r\n                      </td>\r\n                       \r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-left text-sm font-medium\">\r\n                         <button type=\"button\" @click.prevent=\"deleteExpense(i)\" class=\"inline-flex items-center px-2.5 py-1.5 border border-red-300 shadow-sm text-xs font-medium rounded text-red-700 bg-white hover:bg-red-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500\">\r\n                        Delete\r\n                      </button>\r\n                      </td>\r\n                    </tr>\r\n\r\n                    <tr class=\"bg-white\">\r\n                      <td class=\"px-4 py-3 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                        Total Expense\r\n                      </td>\r\n                      <td colspan=\"2\" class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(totalExpense) }}\r\n                      </td>\r\n                       \r\n                    </tr>\r\n                  </tbody>\r\n                </table>\r\n\r\n              </div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n        <!-- Expense Details ================ -->\r\n\r\n        <!-- Parcel Details ================ -->\r\n        <div class=\"flex flex-col w-full\">\r\n          <h1 class=\"text-lg leading-6 font-medium text-gray-900 mb-2\">Parcel Details</h1>\r\n          <div class=\"-my-2 overflow-x-auto sm:-mx-6 lg:-mx-8 \">\r\n            <div class=\"py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8\">\r\n              <div class=\"shadow overflow-hidden border-b border-gray-200 sm:rounded-lg mb-10\">\r\n                <table class=\"min-w-full divide-y divide-gray-200\">\r\n                  <thead class=\"bg-gray-200\">\r\n                    <tr>\r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                        BF\r\n                      </th>\r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                        Current Orders\r\n                      </th>\r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                        Cash Received\r\n                      </th>\r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                        Returns\r\n                      </th>\r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                        Balance\r\n                      </th>\r\n                       \r\n                    </tr>\r\n                  </thead>\r\n                  <tbody>\r\n                \r\n                    <tr class=\"bg-white\">\r\n                      <td class=\"px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                        {{ commaSeperated(preData.parcel_detail_bf) }}\r\n                      </td>\r\n\r\n                      <td class=\"px-6 py-4 whitespace-nowrap text-sm text-gray-900\">\r\n                        <text-input v-model=\"preData.parcel_detail_current_orders\" readonly=\"true\" type=\"text\" class-input=\"py-1\" />\r\n                      </td>\r\n\r\n                      <td class=\"px-6 py-4 whitespace-nowrap text-sm text-gray-900\">\r\n                        <text-input v-model=\"preData.parcel_detail_cash_received\" readonly=\"true\" type=\"text\" class-input=\"py-1\" />\r\n                      </td>\r\n\r\n                      <td class=\"px-6 py-4 whitespace-nowrap text-sm text-gray-900\">\r\n                        <text-input v-model=\"preData.parcel_detail_returns\" readonly=\"true\" type=\"text\" class-input=\"py-1\" />\r\n                      </td>\r\n                     \r\n                      <td class=\"px-6 py-4 whitespace-nowrap text-right text-sm font-medium\">\r\n                        {{ commaSeperated(totalBalanceParcelDetail) }}\r\n                      </td>\r\n                    </tr>\r\n\r\n                    <tr class=\"bg-white\">\r\n                      <td class=\"px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                        {{ commaSeperated(preData.parcel_detail_bf_amount) }}\r\n                      </td>\r\n\r\n                      <td class=\"px-6 py-4 whitespace-nowrap text-sm text-gray-900\">\r\n                        <text-input v-model=\"preData.parcel_detail_current_orders_amount\" readonly=\"true\" type=\"text\" class-input=\"py-1\" />\r\n                      </td>\r\n\r\n                      <td class=\"px-6 py-4 whitespace-nowrap text-sm text-gray-900\">\r\n                        <text-input v-model=\"preData.parcel_detail_cash_received_amount\" readonly=\"true\" type=\"text\" class-input=\"py-1\" />\r\n                      </td>\r\n\r\n                      <td class=\"px-6 py-4 whitespace-nowrap text-sm text-gray-900\">\r\n                        <text-input v-model=\"preData.parcel_detail_returns_amount\" readonly=\"true\" type=\"text\" class-input=\"py-1\" />\r\n                      </td>\r\n                     \r\n                      <td class=\"px-6 py-4 whitespace-nowrap text-right text-sm font-medium\">\r\n                        {{ commaSeperated(totalBalanceAmountParcelDetail) }}\r\n                      </td>\r\n                    </tr>\r\n\r\n \r\n                  </tbody>\r\n                </table>\r\n              </div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n        <!-- Parcel Details ================ -->\r\n\r\n        <!-- Bank Account Details ================ -->\r\n        <div class=\"flex flex-col w-full\">\r\n          <h1 class=\"text-lg leading-6 font-medium text-gray-900 mb-2\">Bank Account Details</h1>\r\n          <div class=\"-my-2 overflow-x-auto sm:-mx-6 lg:-mx-8 \">\r\n            <div class=\"py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8\">\r\n              <div class=\"shadow overflow-hidden border-b border-gray-200 sm:rounded-lg mb-10\">\r\n                <table class=\"min-w-full divide-y divide-gray-200\">\r\n                  <thead class=\"bg-gray-200\">\r\n                    <tr>\r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                        Transaction Detail\r\n                      </th>\r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                       Cash Type\r\n                      </th>\r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                       Amount\r\n                       <button type=\"button\" @click.prevent=\"addBankAccount\" class=\"inline-flex items-center px-2.5 py-1.5 border border-transparent text-xs font-medium rounded shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500\">\r\n                           Add\r\n                        </button>\r\n                      </th>\r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                       Balance\r\n                      </th>\r\n                      <th scope=\"col\" class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">\r\n                       Action\r\n                      </th>\r\n                    </tr>\r\n                  </thead>\r\n                  <tbody>\r\n                     \r\n                     <tr class=\"bg-white\">\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                         BF\r\n                      </td>\r\n                      <td></td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\"></td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(preData.bank_account_detail_bf) }}\r\n                      </td>\r\n                       \r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-left text-sm font-medium\">\r\n                       \r\n                      </td>\r\n                    </tr>\r\n \r\n                    <tr class=\"bg-white\" v-for=\"(e,i) in form.bank_account_details\">\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                        <text-input v-model=\"e.title\" type=\"text\" class-input=\"py-1\" label=\"\" />\r\n                      </td>\r\n                      <td>\r\n                        <fieldset class=\"mt-0\">\r\n                          \r\n                          <div class=\"space-y-4 sm:flex sm:items-center sm:space-y-0 sm:space-x-10\">\r\n                            <div class=\"flex items-center\">\r\n                              <input :id=\"'bank_cash_type_in_'+i\" v-model=\"e.cash_type\" :name=\"'bank_cash_type_'+i\" type=\"radio\" value=\"in\" checked class=\"focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300\">\r\n                              <label :for=\"'bank_cash_type_in_'+i\" class=\"ml-3 block text-sm font-medium text-gray-700\">\r\n                               Cash In\r\n                              </label>\r\n                            </div>\r\n\r\n                            <div class=\"flex items-center\">\r\n                              <input :id=\"'bank_cash_type_out_'+i\" v-model=\"e.cash_type\" :name=\"'bank_cash_type_'+i\" type=\"radio\" value=\"out\" class=\"focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300\">\r\n                              <label :for=\"'bank_cash_type_out_'+i\" class=\"ml-3 block text-sm font-medium text-gray-700\">\r\n                                Cash Out\r\n                              </label>\r\n                            </div>\r\n \r\n                          </div>\r\n                        </fieldset>\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        <text-input v-model=\"e.amount\" type=\"text\" class-input=\"py-1\" label=\"\" placeholder=\"Amount\"/>\r\n                        <!-- <label :for=\"'file-upload_'+i\" class=\"mt-1 inline-block relative cursor-pointer bg-white rounded-md font-medium text-indigo-600 hover:text-indigo-500 focus-within:outline-none focus-within:ring-2 focus-within:ring-offset-2 focus-within:ring-indigo-500\"> -->\r\n                        <!-- <span>Upload Screenshot</span> -->\r\n                        <!-- <input :id=\"'file-upload_'+i\" :name=\"file-upload\" type=\"file\" class=\"sr-only\"> -->\r\n                      <!-- </label> -->\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       {{ commaSeperated(totalBankAccountBalance(i)) }}\r\n                      </td>\r\n                       <td></td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-left text-sm font-medium\">\r\n                         <button type=\"button\" @click.prevent=\"deleteBankAccount(i)\" class=\"inline-flex items-center px-2.5 py-1.5 border border-red-300 shadow-sm text-xs font-medium rounded text-red-700 bg-white hover:bg-red-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500\">\r\n                        Delete\r\n                      </button>\r\n                      </td>\r\n                    </tr>\r\n\r\n                    <tr class=\"bg-white\">\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                        \r\n                      </td>\r\n                      <td>\r\n                         \r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       <strong> Cash In</strong>\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(totalCashIn) }}\r\n                      </td>\r\n                       \r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-left text-sm font-medium\">\r\n                        \r\n                      </td>\r\n                    </tr>\r\n\r\n                    <tr class=\"bg-white\">\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                        \r\n                      </td>\r\n                      <td>\r\n                         \r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       <strong> Cash Out</strong>\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(totalCashOut) }}\r\n                      </td>\r\n                       \r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-left text-sm font-medium\">\r\n                        \r\n                      </td>\r\n                    </tr>\r\n\r\n                    <tr class=\"bg-white\">\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                        \r\n                      </td>\r\n                      <td>\r\n                         \r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       <strong> Final Balance</strong>\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(totalBankAccountBalance(form.bank_account_details.length-1)) }}\r\n                      </td>\r\n                       \r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-left text-sm font-medium\">\r\n                        \r\n                      </td>\r\n                    </tr>\r\n \r\n                  </tbody>\r\n                </table>\r\n\r\n\r\n              </div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n        <!-- Bank Account Details ================ -->\r\n\r\n<!-- =========================================================================================================== -->\r\n<!-- =========================================================================================================== -->\r\n<!-- =========================================================================================================== -->\r\n\r\n        <!-- Print Report ================ -->\r\n        <div id=\"print_report\" class=\"flex flex-col w-full\" style=\"display:none;\">\r\n          <div style=\"margin-right:8px\">\r\n          <!-- <h1 class=\"text-lg leading-6 font-medium text-gray-900 mb-2\">Print</h1> -->\r\n           <!-- <hr> -->\r\n              <h3 class=\"text-lg leading-6 font-medium text-gray-900 mb-2\" style=\"margin-bottom:3px\">Employee Details</h3>\r\n                <table style=\"width:100%;\">\r\n                  <thead class=\"bg-gray-200\">\r\n                     <tr>\r\n                       <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\"> Name</th>\r\n                       <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">Time in </th>\r\n                       <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">Time out</th>\r\n                     </tr>\r\n                  </thead>\r\n                  <tbody>\r\n                     \r\n                     <tr v-for=\"(e,i) in form.employees\" >\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">{{ getUserById(e.user_id).first_name }} {{ getUserById(e.user_id).last_name }}</td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        \r\n                        <span v-if=\"e.time_in != '' && e.time_in != null\">\r\n                        {{ DateTime.fromFormat(\"19:19\", \"hh:mm\").toFormat(\"hh:mm a\") }} </span> \r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       <span v-if=\"e.time_out != '' && e.time_out != null\"> {{ DateTime.fromFormat(e.time_out,'hh:mm').toFormat('hh:mm a') }}</span>\r\n                      </td>\r\n                     </tr>\r\n \r\n                  </tbody>\r\n                </table>\r\n\r\n                <!-- <hr> -->\r\n\r\n               <div style=\"display:flex\">\r\n                 <div style=\"width:50%;margin-right:15px\">\r\n                    <h3 class=\"text-lg leading-6 font-medium text-gray-900 mb-2\" style=\"margin-bottom:3px\">Daily Cash Flow</h3>\r\n                <table style=\"width:100%;\">\r\n                  <thead class=\"bg-gray-200\">\r\n                     <tr>\r\n                        <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\"><strong>Title</strong></th>\r\n                        <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\"><strong>Amount</strong></th>\r\n                     </tr>\r\n                  </thead>\r\n                  <tbody>\r\n                    <tr>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        BF\r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(preData.daily_cash_flow_bf) }}\r\n                      </td>\r\n\r\n                    </tr>\r\n\r\n                     <tr>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        Sale / Recovery\r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       {{ commaSeperated(preData.daily_cash_flow_sales) }}\r\n                      </td> \r\n                    </tr>\r\n\r\n                    <tr>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        Expenses\r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       -{{ commaSeperated(totalExpense) }}\r\n                      </td> \r\n                    </tr>\r\n\r\n                    <tr>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        Bank Deposited\r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       {{ form.daily_cash_flow.bank_deposited }}\r\n                      </td> \r\n                    </tr>\r\n\r\n                    <tr>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        Cash in Hand\r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(totalCashInHand) }}\r\n                      </td> \r\n                    </tr>\r\n                  </tbody>\r\n                </table>\r\n                 </div>\r\n               <div style=\"width:50%;margin-left:15px\">\r\n                   <!-- <hr> -->\r\n                <h3 style=\"margin-bottom:3px\">\r\n                  Expense Detail\r\n                </h3>\r\n                 <table style=\"width:100%;\">\r\n                  <thead class=\"bg-gray-200\">\r\n                    <tr>\r\n                      <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">Title</th>\r\n                      <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">Amount</th>\r\n                    </tr>\r\n                  </thead>\r\n                  <tbody>\r\n     \r\n                    <tr v-for=\"(e,i) in form.expense_details\">\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                      {{ e.title }}\r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ e.amount }}\r\n                      </td>\r\n                    </tr>\r\n                     <tr >\r\n                      <td >\r\n                       <strong> Total Expense</strong>\r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(totalExpense) }}\r\n                      </td>\r\n                       \r\n                    </tr>\r\n                  </tbody>\r\n                </table>\r\n                 </div>\r\n               </div>\r\n\r\n                \r\n           \r\n                <h3 style=\"margin-bottom:3px\">\r\n                  Parcel Details\r\n                </h3>\r\n               <table style=\"width:100%;\">\r\n                  <thead class=\"bg-gray-200\">\r\n                    <tr>\r\n                      <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">BF</th>\r\n                      <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">Current Orders</th>\r\n                      <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">Cash Received</th>\r\n                      <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">Returns</th>\r\n                      <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">Balance</th>\r\n                    </tr>\r\n                  </thead>\r\n                  <tbody>\r\n                     <tr>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(preData.parcel_detail_bf) }}\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(preData.parcel_detail_current_orders) }}\r\n                        \r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n\r\n                        {{ commaSeperated(preData.parcel_detail_cash_received) }}\r\n                       \r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(preData.parcel_detail_returns) }}\r\n                   \r\n                      </td>\r\n                     \r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(totalBalanceParcelDetail) }}\r\n                      </td>\r\n                    </tr>\r\n\r\n                    <tr class=\"bg-white\">\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(preData.parcel_detail_bf_amount) }}\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(preData.parcel_detail_current_orders_amount) }}\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(preData.parcel_detail_cash_received_amount) }}\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(preData.parcel_detail_returns_amount) }}\r\n                      </td>\r\n                     \r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(totalBalanceAmountParcelDetail) }}\r\n                      </td>\r\n                    </tr>\r\n                  </tbody>\r\n                </table>\r\n\r\n                <h3 class=\"text-lg leading-6 font-medium text-gray-900 mb-2\" style=\"margin-bottom:3px\">Bank Account Details</h3>\r\n               \r\n               <table style=\"width:100%;\">\r\n                  <thead class=\"bg-gray-200\">\r\n                    <tr>\r\n                      <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">Transaction Title</th>\r\n                     \r\n                      <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">Cash Type</th>\r\n                      <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">Amount</th>\r\n                      <th class=\"px-6 py-3 text-left text-xs font-medium text-gray-900 uppercase tracking-wider\">Balance</th>\r\n                    </tr>\r\n                  </thead>\r\n                  <tbody>\r\n \r\n                    <tr class=\"bg-white\"> \r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                         BF\r\n                      </td>\r\n                      \r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\"></td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\"></td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(preData.bank_account_detail_bf) }}\r\n                      </td>\r\n                       \r\n                       \r\n                    </tr>\r\n \r\n                    <tr class=\"bg-white\" v-for=\"(e,i) in form.bank_account_details\">\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                        {{ e.title }}\r\n                      </td>\r\n                       <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                         {{ e.cash_type }}\r\n                       </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       {{ commaSeperated(e.amount) }}\r\n                        <!-- <label :for=\"'file-upload_'+i\" class=\"mt-1 inline-block relative cursor-pointer bg-white rounded-md font-medium text-indigo-600 hover:text-indigo-500 focus-within:outline-none focus-within:ring-2 focus-within:ring-offset-2 focus-within:ring-indigo-500\"> -->\r\n                        <!-- <span>Upload Screenshot</span> -->\r\n                        <!-- <input :id=\"'file-upload_'+i\" :name=\"file-upload\" type=\"file\" class=\"sr-only\"> -->\r\n                      <!-- </label> -->\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       {{ commaSeperated(totalBankAccountBalance(i)) }}\r\n                      </td>\r\n                        \r\n                      \r\n                    </tr>\r\n\r\n                    <tr class=\"bg-white\">\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                        \r\n                      </td>\r\n                      <td></td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       <strong> Cash In</strong>\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(totalCashIn) }}\r\n                      </td>\r\n                       \r\n                      \r\n                    </tr>\r\n\r\n                    <tr class=\"bg-white\">\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                        \r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                         \r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       <strong> Cash Out</strong>\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(totalCashOut) }}\r\n                      </td>\r\n                       \r\n                     \r\n                    </tr>\r\n\r\n                    <tr class=\"bg-white\">\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm font-medium text-gray-900\">\r\n                        \r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                         \r\n                      </td>\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                       <strong> Final Balance</strong>\r\n                      </td>\r\n\r\n                      <td class=\"px-4 py-1 whitespace-nowrap text-sm text-gray-900\">\r\n                        {{ commaSeperated(totalBankAccountBalance(form.bank_account_details.length-1)) }}\r\n                      </td>\r\n                     \r\n                    </tr>\r\n\r\n                  </tbody>\r\n                </table>\r\n        </div>\r\n        </div>\r\n        <!-- Print Report ================ -->\r\n           \r\n         <!--  <text-input v-model=\"form.date_max\" :error=\"form.errors.date_max\" type=\"date\" class=\"pb-8 pr-6 w-full lg:w-1/2\" label=\"Date Max\" /> -->\r\n\r\n        <!--   <text-input v-model=\"form.first_name\" :error=\"form.errors.first_name\" class=\"pb-8 pr-6 w-full lg:w-1/2\" label=\"First name\" />\r\n          <text-input v-model=\"form.last_name\" :error=\"form.errors.last_name\" class=\"pb-8 pr-6 w-full lg:w-1/2\" label=\"Last name\" />\r\n          <text-input v-model=\"form.email\" :error=\"form.errors.email\" class=\"pb-8 pr-6 w-full lg:w-1/2\" label=\"Email\" />\r\n          <text-input v-model=\"form.password\" :error=\"form.errors.password\" class=\"pb-8 pr-6 w-full lg:w-1/2\" type=\"password\" autocomplete=\"new-password\" label=\"Password\" />\r\n          <select-input v-model=\"form.owner\" :error=\"form.errors.owner\" class=\"pb-8 pr-6 w-full lg:w-1/2\" label=\"Owner\">\r\n            <option :value=\"true\">Yes</option>\r\n            <option :value=\"false\">No</option>\r\n          </select-input>\r\n          <file-input v-model=\"form.photo\" :error=\"form.errors.photo\" class=\"pb-8 pr-6 w-full lg:w-1/2\" type=\"file\" accept=\"image/*\" label=\"Photo\" /> -->\r\n        </div>\r\n\r\n        <div class=\"flex items-center justify-end px-8 py-4 bg-gray-50 border-t border-gray-100 mb-12\">\r\n          <span class=\"btn-indigo mr-2\" @click=\"print\" >Print</span>\r\n          <loading-button :loading=\"form.processing\" class=\"btn-indigo\" type=\"submit\">Add Report</loading-button>\r\n        </div>\r\n\r\n      </form>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport { Head, Link } from '@inertiajs/inertia-vue3'\r\nimport Layout from '@/Shared/Layout'\r\nimport FileInput from '@/Shared/FileInput'\r\nimport TextInput from '@/Shared/TextInput'\r\nimport SelectInput from '@/Shared/SelectInput'\r\nimport LoadingButton from '@/Shared/LoadingButton'\r\nconst { DateTime } = require(\"luxon\");\r\n \r\nexport default {\r\n  components: {\r\n    FileInput,\r\n    Head,\r\n    Link,\r\n    LoadingButton,\r\n    SelectInput,\r\n    TextInput,\r\n  },\r\n  props:{\r\n    dateMin:String,\r\n    dateMax:String,\r\n    preData:Object,\r\n  },\r\n  layout: Layout,\r\n  // remember: 'form',\r\n  data() {\r\n    return {\r\n      DateTime,\r\n      form: this.$inertia.form({\r\n        date_min: this.dateMin,\r\n        date_max: this.dateMax,\r\n        pendingOrders:[],\r\n        daily_cash_flow:{\r\n          bank_deposited:0,\r\n          cash_in_hand:0,\r\n        },\r\n        employees:[\r\n        ],\r\n        expense_details:[\r\n        ],\r\n        parcel_details:{\r\n        },\r\n        bank_account_details:[\r\n        ]\r\n      }),\r\n    }\r\n  },\r\n  computed:{\r\n    \r\n    dayInString(){\r\n      return DateTime.fromFormat(this.form.date_min,'yyyy-MM-dd').toFormat('cccc');\r\n    },\r\n    // Daily Flow\r\n    totalCashInHand(){\r\n\r\n      var bd = this.form.daily_cash_flow.bank_deposited;\r\n      if(this.isNaN(bd)){\r\n        bd = 0;\r\n      }\r\n    \r\n      return (parseFloat(this.preData.daily_cash_flow_bf) + parseFloat(this.preData.daily_cash_flow_sales)) - (parseFloat(this.totalExpense) + parseFloat(bd));\r\n    },\r\n\r\n    // Expense Details\r\n    totalExpense(){\r\n      var t = this.form.expense_details.map(item => item.amount).reduce((prev, curr) => {\r\n        return this.parseAmount(prev, curr)\r\n      }, 0);\r\n\r\n      if(this.isNaN(t)){\r\n        return 0\r\n      }\r\n      return t;\r\n    },\r\n    totalCashIn(){\r\n      var t = this.form.bank_account_details.map(item => {\r\n        if(item.cash_type == 'in'){\r\n          return item.amount;\r\n        }\r\n      }).reduce((prev, curr) => {\r\n         \r\n        return this.parseAmount(prev, curr)\r\n      }, 0);\r\n\r\n      if(this.isNaN(t)){\r\n        return 0\r\n      }\r\n      return t;\r\n    },\r\n    totalCashOut(){\r\n      var t = this.form.bank_account_details.map(item => {\r\n        if(item.cash_type == 'out'){\r\n          return item.amount;\r\n        }\r\n      }).reduce((prev, curr) => {\r\n         return this.parseAmount(prev, curr)\r\n      }, 0);\r\n\r\n      if(this.isNaN(t)){\r\n        return 0\r\n      }\r\n      return t;\r\n    },\r\n    totalBalanceParcelDetail(){\r\n        var current_orders = this.preData.parcel_detail_current_orders \r\n        var cash_received = this.preData.parcel_detail_cash_received \r\n        var returns = this.preData.parcel_detail_returns\r\n\r\n        if(this.isNaN(current_orders)){\r\n          current_orders = 0\r\n        }else{\r\n          current_orders = parseInt(current_orders)\r\n        }\r\n\r\n        if(this.isNaN(cash_received)){\r\n          cash_received = 0\r\n        }else{\r\n          cash_received = parseInt(cash_received)\r\n        }\r\n\r\n        if(this.isNaN(returns)){\r\n          returns = 0\r\n        }else{\r\n          returns = parseInt(returns)\r\n        }\r\n\r\n        return (current_orders + parseInt(this.preData.parcel_detail_bf))-(cash_received+returns)\r\n    },\r\n    totalBalanceAmountParcelDetail(){\r\n        var current_orders = this.preData.parcel_detail_current_orders_amount \r\n        var cash_received = this.preData.parcel_detail_cash_received_amount \r\n        var returns = this.preData.parcel_detail_returns_amount\r\n        \r\n        if(this.isNaN(current_orders)){\r\n          current_orders = 0\r\n        }else{\r\n          current_orders = parseFloat(current_orders)\r\n        }\r\n\r\n        if(this.isNaN(cash_received)){\r\n          cash_received = 0\r\n        }else{\r\n          cash_received = parseFloat(cash_received)\r\n        }\r\n\r\n        if(this.isNaN(returns)){\r\n          returns = 0\r\n        }else{\r\n          returns = parseFloat(returns)\r\n        }\r\n\r\n        return (current_orders + parseFloat(this.preData.parcel_detail_bf_amount))-(cash_received+returns)\r\n    }\r\n  },\r\n  methods: {\r\n    commaSeperated(val){\r\n      if(val == null){\r\n        return '0';\r\n      }\r\n      var parts = val.toString().split(\".\");\r\n      parts[0] = parts[0].replace(/\\B(?=(\\d{3})+(?!\\d))/g, \",\");\r\n      val = parts.join(\".\");\r\n      return val;\r\n    },\r\n    isNaN(num){\r\n      if(num == \"\" || num == null || isNaN(num)){\r\n        return true\r\n      }\r\n      return false;\r\n    },\r\n    parseAmount(prev,curr){\r\n      if(this.isNaN(prev)){\r\n        curr = Math.abs(curr)\r\n        return curr;\r\n      }\r\n      if(this.isNaN(curr)){\r\n\r\n        prev = Math.abs(prev)\r\n        return prev;\r\n      }\r\n      curr = Math.abs(curr)\r\n      prev = Math.abs(prev)\r\n      return parseFloat(prev) + parseFloat(curr)\r\n      ;\r\n    },\r\n    addEmployee(){\r\n     this.form.employees.push({\r\n        user_id:\"\",\r\n        time_in:\"\",\r\n        time_out:\"\",\r\n      })\r\n    },\r\n    deleteEmployee(i){\r\n     this.form.employees.splice(i,1)\r\n    },\r\n    addExpense(){\r\n      this.form.expense_details.push({\r\n        title:\"\",\r\n        amount:null,\r\n      })\r\n    },\r\n    deleteExpense(i){\r\n       this.form.expense_details.splice(i,1)\r\n    },\r\n    addBankAccount(){\r\n      this.form.bank_account_details.push({\r\n        title:\"\",\r\n        cash_type:\"out\",\r\n        amount:null,\r\n        bf:\"\",\r\n      })\r\n    },\r\n    deleteBankAccount(i){\r\n       this.form.bank_account_details.splice(i,1)\r\n    },\r\n    totalBankAccountBalance(i){\r\n      if(i == -1){\r\n        return 0;\r\n      }\r\n\r\n      var item = this.form.bank_account_details[i];\r\n      var itemAmount = item.amount;\r\n      if(itemAmount == \"\" || itemAmount == null || isNaN(itemAmount)){\r\n        itemAmount = 0;\r\n      }else{\r\n        itemAmount = parseFloat(itemAmount)\r\n      }\r\n      var balance = 0;\r\n      if(i == 0){\r\n        if(item.cash_type == 'in'){\r\n          balance = parseFloat(this.preData.bank_account_detail_bf) + itemAmount;\r\n        }else{\r\n          balance = parseFloat(this.preData.bank_account_detail_bf) - itemAmount;\r\n        }\r\n        this.form.bank_account_details[0].bf = balance;\r\n      }else{\r\n        if(item.cash_type == 'in'){\r\n          balance = this.totalBankAccountBalance(i-1) + itemAmount;\r\n        }else{\r\n          balance = this.totalBankAccountBalance(i-1) - itemAmount;\r\n        }\r\n        this.form.bank_account_details[i].bf = balance;\r\n      }\r\n\r\n\r\n\r\n      return balance\r\n    },\r\n    store() {\r\n\r\n      // Bank Final balance\r\n      this.form.parcel_details.bf = this.totalBalanceParcelDetail;\r\n      this.form.parcel_details.bf_amount = this.totalBalanceAmountParcelDetail;\r\n\r\n      this.form.daily_cash_flow.cash_in_hand = this.totalCashInHand;\r\n\r\n      this.form.post('/daily-report/add')\r\n    },\r\n\r\n    getUserById(id){\r\n      var user = this.preData.users.filter(item => {\r\n        return item.id == id\r\n      })\r\n      if(user.length > 0){\r\n        return user[0]\r\n      }else{\r\n        return {}\r\n      }\r\n    },\r\n    print(){\r\n      var mywindow = window.open('', 'PRINT', 'height=700,width=1000');\r\n      mywindow.document.write('<html><head><title>' + document.title  + '</title>');\r\n      mywindow.document.write('<style>');\r\n      mywindow.document.write(`\r\n        table,body {\r\n  font-family: arial, sans-serif;\r\n  border-collapse: collapse;\r\n  width: 100%;\r\n}\r\n\r\ntd, th {\r\n  border: 1px solid #dddddd;\r\n  text-align: left;\r\n  padding: 8px;\r\n}\r\n\r\ntr:nth-child(even) {\r\n  background-color: #fff;\r\n}\r\n      `);\r\n      mywindow.document.write('</style>');\r\n      mywindow.document.write('</head><body >');\r\n      mywindow.document.write('<h1> Daily Report: ' + DateTime.fromFormat(this.form.date_min,'yyyy-MM-dd').toFormat('dd MMMM yyyy')  + '</h1>');\r\n      mywindow.document.write(document.getElementById('print_report').innerHTML);\r\n      mywindow.document.write('</body></html>');\r\n\r\n      mywindow.print();\r\n      // mywindow.close();\r\n\r\n      return true;\r\n    }\r\n  },\r\n  mounted(){\r\n    this.form.date_min = this.dateMin;\r\n\r\n    this.form.parcel_details = {\r\n        current_orders:this.preData.parcel_detail_current_orders,\r\n        cash_received:this.preData.parcel_detail_cash_received,\r\n        returns:this.preData.parcel_detail_returns,\r\n        current_orders_amount:this.preData.parcel_detail_current_orders_amount,\r\n        cash_received_amount:this.preData.parcel_detail_cash_received_amount,\r\n        returns_amount:this.preData.parcel_detail_current_orders_amount,\r\n      };\r\n    this.form.daily_cash_flow.bank_deposited = this.preData.daily_cash_flow_bank_deposited\r\n\r\n    this.form.pendingOrders = this.preData.pendingOrders \r\n\r\n    this.form.expense_details = this.preData.expense_details\r\n\r\n    this.form.bank_account_details = this.preData.bank_account_details\r\n    this.form.employees = this.preData.employees\r\n\r\n  },\r\n  watch:{\r\n    totalBalanceParcelDetail :function(val){\r\n      this.form.parcel_details.bf = val\r\n    },\r\n    totalBalanceAmountParcelDetail :function(val){\r\n      this.form.parcel_details.bf_amount = val\r\n    },\r\n    \"form.date_min\":function(val){\r\n      this.$inertia.get('/daily-report/add', {\r\n        date: val,\r\n         \r\n      })\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<style>\r\n@media print {\r\n    .myDivToPrint {\r\n        background-color: white;\r\n        height: 100%;\r\n        width: 100%;\r\n        position: fixed;\r\n        top: 0;\r\n        left: 0;\r\n        margin: 0;\r\n        padding: 15px;\r\n        font-size: 14px;\r\n        line-height: 18px;\r\n    }\r\n}\r\n</style>"],"sourceRoot":""}]);
